import React from 'react'
import { Form, Input } from 'antd'
import type { Resume } from '@/typings/configType'
interface props {
  setResumn: (val: any, key: string) => void
  profile: Resume['profile']
}

export default function (props: props) {
  const { setResumn, profile } = props
  return (
    <>
      <Form.Item label="">
        <Input placeholder="" value={profile?.name} onChange={(e) => setResumn(e.target.value, 'profile.name')} maxLength={8} />
      </Form.Item>
      <Form.Item label="">
        <Input placeholder="" value={profile?.mobile} onChange={(e) => setResumn(e.target.value, 'profile.mobile')} maxLength={11} />
      </Form.Item>
      <Form.Item label="">
        <Input placeholder="" value={profile?.email} onChange={(e) => setResumn(e.target.value, 'profile.email')} maxLength={22} />
      </Form.Item>
      <Form.Item label="gihub">
        <Input placeholder="gihub" value={profile?.github} onChange={(e) => setResumn(e.target.value, 'profile.github')} maxLength={22} />
      </Form.Item>
    </>
  )
}
